import React from 'react'
import { clock } from 'assets/index'
import './Time.css'

export default ({ cardType, startTime, endTime }) => {

    var startDate = new Date(startTime)
    var endDate = endTime ? new Date(endTime) : new Date()

    var minutes = Math.round((endDate.getTime() - startDate.getTime()) / 60000)

    return (
        cardType === 'walker' ?
            <div className='textCenter lightishText'>
                <b>
                    {minutes} Min
                </b>
            </div>
            : <div className='Time lightishText'>
                <div className='text font-xs'>
                    1:12 PM
                </div>
                <img className='clock' src={clock} alt='funwalks dogs' />
                <div className='text font-xs'>
                    1:42 PM
            </div>
            </div>
    )
}


