import React from 'react'
import { Button } from 'components'
import { upload, edit } from 'assets'

const CardActions = ({ state = 3 }) => {

    if (state > 3)
        return <div></div>

    return (
        <div className='CardActions padding-top-sm'>

            <Button>
                <img src={edit} alt='toronto' />
            </Button>

            {

                state <= 2 ?
                    <div className='padding-right-left-sm' style={{ width: '300%' }}>
                        <Button
                            fontSize={12}
                        >
                            {state === 1 ? 'END WALK' : 'RESTART'}
                        </Button>
                    </div>
                    : <div className='padding-right-left-xs'></div>

            }

            <Button>
                <img src={upload} alt='toronto dog walkers' />
            </Button>
        </div>
    )
}

export default CardActions




