import React from 'react'
import { connect } from 'react-redux'
import { Link } from "react-router-dom"
import { TextField, Button } from 'components'
import { arrowLeft } from 'assets/index'
import Select from './components/Select'
import './Register.css'

const Register = ({ typeUserChange, emailChange, passwordChange, userNameChange, confirmPasswordChange }) => (
    <div className="RegisterView pageTransition padding-right-left-md padding-top-bottom-md">
        <Link to='/' className='back font-sm'>
            <img src={arrowLeft} alt='' />
            LOGIN
        </Link>
        <h1 className='padding-top-md'>Register</h1>
        <Select onChange={typeUserChange} />
        <div className='field'>
            <TextField
                label='User Name'
                placeholder='User Name'
                onChange={userNameChange}
            />
        </div>
        <div className='field'>
            <TextField
                label='Your Email'
                placeholder='Your Email'
                onChange={emailChange}
            />
        </div>
        <div className='field'>
            <TextField
                label='Password'
                placeholder='Password'
                type='password'
                onChange={passwordChange}
            />
        </div>
        <div className='field'>
            <TextField
                label='Confirm Password'
                placeholder='Confirm Password'
                type='password'
                onChange={confirmPasswordChange}
            />
        </div>
        <div className='field'>
            <Link to='/owner'>
                <Button>Register</Button>
            </Link>
        </div>
    </div>
)

export default connect(
    () => ({}),
    dispatch => ({
        typeUserChange: userType =>
            dispatch({
                type: 'REGISTER_USER_TYPE_CHANGE',
                payload: userType
            }),
        userNameChange: username =>
            dispatch({
                type: 'REGISTER_USERNAME_CHANGE',
                payload: username
            }),

        emailChange: text =>
            dispatch({
                type: 'REGISTER_EMAIL_CHANGE',
                payload: text
            }),

        passwordChange: text =>
            dispatch({
                type: 'REGISTER_PASSWORD_CHANGE',
                payload: text
            }),

        confirmPasswordChange: text =>
            dispatch({
                type: 'REGISTER_CONFIRM_PASSWORD_CHANGE',
                payload: text
            })
    })
)(Register)


