import React, { Component } from 'react'

import Dialog from '@material-ui/core/Dialog'
import Zoom from '@material-ui/core/Zoom'

import CardMain from './CardMain'

import './Card.css'

class Card extends Component {

    state = { isOpen: false }


    close = (() =>
        this.setState({ isOpen: false })
    ).bind(this)

    open = (() =>
        this.setState({ isOpen: true })
    ).bind(this)

    render() {

        const { isNew, cardType, walk } = this.props
        const { isOpen } = this.state

        return (

            <div>
                <CardMain
                    isNew={isNew}
                    cardType={cardType}
                    onClick={this.open}
                    walk={walk}
                />

                {
                    isOpen &&
                    <Dialog
                        open={isOpen}
                        TransitionComponent={props => <Zoom {...props} />}
                        fullWidth={true}
                        onClose={this.close}
                        classes={{ paper: 'extandedWrap' }}
                    >
                        <CardMain
                            cardType={cardType}
                            isOpen={isOpen}
                            isNew={isNew}
                            onClose={this.close}
                            walk={walk}
                        />
                    </Dialog>
                }
            </div>

        )
    }
}

export default Card




