import { combineReducers } from 'redux'

import LoginReducer from 'pages/Login/state'
// import OwnerReducer from 'pages/Owner/state'
import RegisterReducer from 'pages/Register/state'
// import WalkerRiducer from 'pages/Walker/state'

import WalksReducer from './walksReducer'

export default combineReducers({
    login: LoginReducer,
    // owner: OwnerReducer,
    register: RegisterReducer,
    // walker: WalkerRiducer,

    walks: WalksReducer
})



