import React from 'react'
import SwipeableGallery from './SwipeableGallery'
import ScrollableGallery from './ScrollableGallery'
import './Gallery.css'

const Gallery = ({ canDownload, isOpen, isNew, images }) => (
    isOpen ?
        <SwipeableGallery
            images={images}
            canDownload={canDownload}
        />
        : <ScrollableGallery
            isNew={isNew}
            isOpen={isOpen}
            canDownload={canDownload}
            images={images}
        />
)

export default Gallery


