import React from 'react'
import Paper from '@material-ui/core/Paper'
import { Logo } from 'components'
import { burger, close } from 'assets/index'
import { Button } from 'components'
import { Link } from 'react-router-dom'

import './Menu.css'

class Menu extends React.Component {

    state = {
        open: null
    }

    toggle() {

        this.setState({ open: !this.state.open })
    }

    render() {
        const { open } = this.state
        let animationClass = ''
        if (open) animationClass = 'slideInDown'
        else if (open !== null) animationClass = 'slideOutUp'

        return (
            <div className={`MenuView ${open ? 'open' : ''}`}>
                <Paper
                    square={true}
                    className='MenuHead'
                >
                    <div className='logo'>
                        <Logo />
                    </div>
                    <b className='title'>
                        {this.props.label}
                    </b>
                    <div onClick={this.toggle.bind(this)} className='icon'>
                        <img src={open ? close : burger} alt='funwalks dog walking app' />
                    </div>
                </Paper>

                {
                    animationClass
                    &&
                    <div className={`menuBody ${animationClass} primaryBg`}>
                        <div className='main padding-right-left-md'>
                            <Link
                                to='/walker'
                                className='item'
                            >
                                <div>Invite Your Walker</div>
                            </Link>
                            <Link
                                to='/owner'
                                className='item'>
                                <div >Edit Account Information</div>
                            </Link>
                            <div className='item'>Privacy Policy</div>
                        </div>
                        <div className='padding-top-bottom-md padding-right-left-md'>
                            <Link to='/'>
                                <Button >LOGOUT</Button>
                            </Link>
                        </div>
                    </div>
                }
            </div>
        )
    }
}

export default Menu


