import React from 'react'
import { Image } from 'components'

const ScrollableGallery = ({ canDownload, isNew, isOpen, images }) => {
    let height = '40px'
    if (isNew) height = '110px'
    if (isOpen) height = '100%'

    return <div className='Gallery' style={{ height }}>
        {images && images.map((url, i) => (
            <Image
                key={i}
                url={url}
                canDownload={canDownload}
            />))}
    </div>
}

export default ScrollableGallery


