import React from 'react'
import { WalkNote, Gallery } from 'components'

const CardBody = ({ isOpen, isNew, cardType, walk }) => {

    const isGalleryDownloadAble = (cardType !== 'walker') && (isOpen || isNew)

    return (
        <div className='cardBody'>
            <div className='padding-top-bottom-sm walkNoteWrap'>
                <WalkNote
                    text={walk.note || 'Attach a Note'}
                />
            </div>
            <div className='galleryWrap'>
                {
                    walk.images ?
                        <Gallery
                            images={walk.images}
                            isOpen={isOpen}
                            isNew={isNew}
                            canDownload={isGalleryDownloadAble}
                        />
                        : <div className='font-sm'>There are no Uploaded images</div>
                }
            </div>
        </div>
    )
}

export default CardBody




