import React from 'react'

import Paper from '@material-ui/core/Paper'

import CardHeader from './CardHeader'
import CardBody from './CardBody'
import CardActions from './CardActions'

const CardMain = ({ cardType, isOpen, isNew, onClick, onClose, walk }) => (
    <Paper
        className={`${isOpen || isNew ? 'BigCard' : 'SmallCard'} ${cardType} textCenter padding-sm`}
        onClick={(e) => (typeof onClick === 'function' && onClick(e))}
    >
        <CardHeader
            cardType={cardType}
            isOpen={isOpen}
            onClose={onClose}
            walk={walk}
        />
        <CardBody
            cardType={cardType}
            isOpen={isOpen}
            isNew={isNew}
            walk={walk}
        />
        {
            cardType === 'walker' &&
            <CardActions
                cardType={cardType}
                state={walk.state}
            />
        }
    </Paper>
)

export default CardMain




