import React from 'react'
import Input from '@material-ui/core/Input'
import './TextField.css'

const styles = {
    label: {
        paddingBottom: 5,
        fontWeight: 'bold'
    },
    input: {
        width: 'calc(100% - 20px)',
        backgroundColor: '#fff',
        padding: '10px 0px 5px 20px',
        fontSize: '14px'
    }
}

export default ({ placeholder, label, onChange, type, disabled = false }) => (
    <div>
        {label && <div style={styles.label}>{label}</div>}
        <Input
            placeholder={placeholder}
            type={type || 'text'}
            disabled={disabled}
            style={styles.input}
            onChange={(event) => onChange && onChange(event.target.value)}
        />
    </div>
)


