import React from 'react'
import { Time } from 'components'
import { close } from 'assets'

const CardHeader = ({ cardType, isOpen, onClose, walk }) => {

    const isWalker = cardType === 'walker'

    return (
        <div>
            {
                isOpen &&
                <div className='textRight' onClick={onClose}>
                    <img src={close} alt='Toronto Dog Walking' />
                </div>
            }
            <div className='cardHeader font-sm'>
                <p className={`item`}>
                    <b>
                        {
                            isWalker ? walk.dogName.toUpperCase() : 'Tuesday July 17'
                        }
                    </b>
                </p>

                <div className='item'>
                    <Time
                        cardType={cardType}
                        startTime={walk.startTime}
                        endTime={walk.endTime}
                    />
                </div>

                {
                    isWalker &&
                    walk.state === 4 &&
                    <div className='item'>
                        21/7/2018
                </div>
                }
            </div>
        </div>
    )
}
export default CardHeader




