import React from 'react'
import Button from '@material-ui/core/Button'

export default ({ children, width = '100%', height = 'auto', boxShadow, fontSize }) => (
    <Button
        variant="contained"
        color="primary"
        style={{
            width,
            height,
            boxShadow,
            fontSize,
            padding: '9px',
            minWidth: 'unset',
            minHeight: 'unset'
        }}
    >
        {children}
    </Button>
)


