import { puppy } from 'assets'

function fakeWalks() {
  const walks = []

  for (let i = 0; i < 2; i++)
    walks.push({
      state: 1,
      startTime: new Date((new Date()).getTime() - 10 * 60000).getTime(),
      endTime: null,
      dogName: 'Bella',
      note: null,
      images: null
    })

  walks.push({
    state: 2,
    startTime: new Date((new Date()).getTime() - 27 * 60000).getTime(),
    endTime: null,
    dogName: 'Bella',
    note: 'Bella was happy and finished all her food.',
    images: [puppy, puppy, puppy, puppy, puppy]
  })

  for (let i = 0; i < 3; i++)
    walks.push({
      state: 3,
      startTime: new Date((new Date()).getTime() - 27 * 60000).getTime(),
      endTime: null,
      dogName: 'Bella',
      note: 'Bella was happy and finished all her food.',
      images: [puppy, puppy, puppy, puppy, puppy]
    })

  for (let i = 0; i < 3; i++)
    walks.push({
      state: 4,
      startTime: new Date((new Date()).getTime() - 27 * 60000).getTime(),
      endTime: null,
      dogName: 'Bella',
      note: 'Bella was happy and finished all her food.',
      images: [puppy, puppy, puppy, puppy, puppy]
    })

  return walks
}

const initialState = fakeWalks()

export default function (state = initialState, action) {
  return state
}


