import { createMuiTheme } from '@material-ui/core/styles'

export default createMuiTheme({
    palette: {
        primary: {
            main: '#000'
        }
    },
    typography: {
        fontSize: 16,
        borderRadius: 0,
    },
    overrides: {
        MuiButton: {
            root: {
                borderRadius: 0,
                fontSize: 14,
            },
        },
        MuiFormControlLabel: {
            root: {
                marginRight: 0
            },
        },
        MuiPaper: {
            root: {
                boxShadow: 'none'
            },
        }
    }
})


