import React from 'react'
import { Button } from 'components'
import { download } from 'assets'
import { downloadImage } from 'services/util'
import './Image.css'

export default ({ url, canDownload }) => (
    <div className='singleImage'  >
        <div className='Image'>
            <img src={url} alt='fun walks' />
            {
                canDownload
                &&
                <div className='download' onClick={
                    (e) => {
                        e.stopPropagation()
                        downloadImage(url, 'puppy.png')
                    }}>
                    <Button
                        height='20px'
                        width='20px'
                        boxShadow='none'
                    >
                        <img
                            className='downloadImage'
                            src={download}
                            alt='funwalks dog walking'
                        />
                    </Button>
                </div>
            }
        </div>
    </div>
)


