import React from 'react'
import Radio from '@material-ui/core/Radio'
import FormControlLabel from '@material-ui/core/FormControlLabel'

const dogOwner = 'Dog Owner'
const dogWalker = 'Dog Walker'

class Select extends React.Component {
    state = {
        selectedValue: null,
    }

    handleChange = event => {
        const { value } = event.target
        this.setState({ selectedValue: value })
        this.props.onChange(value)
    };

    render() {
        return (
            <div>
                <div style={{
                    paddingBottom: 5,
                    fontSize: 16,
                    fontWeight: 'bold'
                }}>
                    User Type
                </div>
                <div className='selectRow'>
                    <FormControlLabel
                        control={<Radio
                            color="primary"
                            value={'owner'}
                            onChange={this.handleChange}
                            checked={this.state.selectedValue === 'owner'}
                            disableRipple={true}
                        />}
                        label={dogOwner}
                    />
                    <FormControlLabel
                        control={<Radio
                            color="primary"
                            value={'walker'}
                            onChange={this.handleChange}
                            checked={this.state.selectedValue === 'walker'}
                            disableRipple={true}
                        />}
                        label={dogWalker}
                    />
                </div>
            </div >
        );
    }
}

export default Select


