import React from 'react'
import Switch from '@material-ui/core/Switch'
import FormControlLabel from '@material-ui/core/FormControlLabel';

export default ({ label, onChange }) => (
    <FormControlLabel
        style={{
            width: '100%',
            margin: 0,
            display: 'flex',
            fontSize: 16,
            position: 'relative',
            right: '14px'
        }}
        control={
            <Switch
                onChange={(e, checked) => onChange(checked)}
                value={label}
                color='primary'
                style={{
                    marginRight: 'auto'
                }}
            />
        }
        label={label}
    />
)


