import React from 'react'
import { Link } from "react-router-dom"
import { connect } from 'react-redux'
import { Logo, TextField, Button } from 'components'
import Switch from './components/Switch'
import Paper from '@material-ui/core/Paper'
import './Login.css'

const Login = ({ emailChange, passwordChange, rememberChange }) => (
    <div className='LoginView pageTransition'>
        <div className="main padding-right-left-md padding-top-bottom-md">
            <div className='header'>
                <div className="logo">
                    <Logo />
                </div>
                <h1>
                    Fun Walks
                </h1>
            </div>
            <TextField
                label='Your Email'
                placeholder='Your Email'
                onChange={emailChange}
            />
            <br />
            <TextField
                label='Your Password '
                placeholder='Your Password'
                type="password"
                onChange={passwordChange}
            />
            <Switch
                label='Remember Me'
                onChange={rememberChange}
            />
            <Link to='/owner'>
                <Button>Login</Button>
            </Link>
        </div>
        <Paper square={true} className="footer padding-right-left-md">
            <span>Forgot Password?</span>
            <Link to='/register'>
                <span>New User</span>
            </Link>
        </Paper>
    </div>
)


const mapDispatchToProps = dispatch => {
    return {
        emailChange: text =>
            dispatch({
                type: 'LOGIN_EMAIL_CHANGE',
                payload: text
            }),

        passwordChange: text =>
            dispatch({
                type: 'LOGIN_PASSWORD_CHANGE',
                payload: text
            }),

        rememberChange: bool =>
            dispatch({
                type: 'LOGIN_REMEMBER_CHANGE',
                payload: bool
            })
    }
}

export default connect(
    () => ({}),
    mapDispatchToProps
)(Login)


