import React from 'react'
import { connect } from 'react-redux'
import { Menu, Card, Button } from 'components'
import Paper from '@material-ui/core/Paper'
import './walker.css'

const Walker = ({ walks }) => {

    const activeWalks = walks.filter(({ state }) => state === 1)
    const restartAbleWalks = walks.filter(({ state }) => state === 2)
    const editAbleWalks = walks.filter(({ state }) => state === 3)
    const oldWalks = walks.filter(({ state }) => state === 4)

    return (
        <div className='WalkerView pageTransition withMenu'>
            <Menu />
            <div className='main'>
                {/* Active Walks */}
                <div className='bgColor1 padding-right-left-sm padding-top-sm'>
                    {activeWalks.map((walk, i) =>
                        <div className='padding-bottom-sm' key={i}>
                            <Card
                                cardType='walker'
                                walk={walk}
                            />
                        </div>
                    )}
                </div>
                {/* RestartAble Walks */}
                <div className='bgColor2 padding-right-left-sm padding-top-sm'>
                    {restartAbleWalks.map((walk, i) =>
                        <div className='padding-bottom-sm' key={i}>
                            <Card
                                cardType='walker'
                                walk={walk}
                            />
                        </div>
                    )}
                </div>
                {/* EditAble Walks */}
                <div className='bgColor3 padding-right-left-sm padding-top-sm'>
                    {editAbleWalks.map((walk, i) =>
                        <div className='padding-bottom-sm' key={i}>
                            <Card
                                cardType='walker'
                                walk={walk}
                            />
                        </div>
                    )}
                </div>
                {/* old Walks */}
                <div className='padding-right-left-sm padding-top-sm'>
                    {oldWalks.map((walk, i) =>
                        <div className='padding-bottom-sm' key={i}>
                            <Card
                                cardType='walker'
                                walk={walk}
                            />
                        </div>
                    )}
                </div>
            </div>
            <Paper
                className='footer padding-sm'
                square={true}
            >
                <Button>
                    START A WALK
                </Button>
            </Paper>

        </div>
    )
}

export default connect(
    ({ walks }) => ({ walks }),
    () => ({})
)(Walker)


