import React from 'react'
import { connect } from 'react-redux'
import { Menu, Card } from 'components'

const Owner = ({ walks }) => {

    return (
        <div className='OwnerView pageTransition withMenu'>
            <Menu label='BELLA' />
            <div className='main'>

                <div className='bgColor1 padding-right-left-sm padding-top-sm'>
                    <div className='padding-bottom-sm'>
                        <Card
                            isNew={true}
                            walk={walks[0]}
                        />
                    </div>
                </div>
                <div className='padding-right-left-sm padding-top-sm'>
                    {
                        walks.map((walk, i) =>
                            (i > 0) ?
                                <div className='padding-bottom-sm' key={i}>
                                    <Card
                                        walk={walk}
                                    />
                                </div>
                                : <div></div>
                        )
                    }
                </div>
            </div>
        </div>
    )
}

export default connect(
    ({ walks }) => ({ walks }),
    () => ({})
)(Owner)


